
.service-component{
    position: relative;
    width: 100%;
    text-align: center;
    justify-content: center;
    padding: 10px;

    background-color: rgba(3, 129, 129, 0.6);
    transition: 0.5s ease;
}


.exp-card-img{
    transform: scale(1);
    transition: transform 0.5s ease;
}

.exp-card-img:hover{
    transform: scale(1.2);
}

.overflow{
    overflow: hidden;
}

.exp-card{
    width: 20rem;
    margin-top: 10px;
    margin-bottom: 20px;

}

.exp-card:hover{
    box-shadow: 10px 10px 10px 10px rgba(2, 46, 53, 0.54),0 17px 50px 0 rgba(0, 0, 0, 0.54);
}