
  .exp-component{
    position: relative;
    align-items: center;
    width: 100%;
    text-align: center;
    justify-content: center;
    margin-left: 0px;
    margin-right: 0px; 
    background-color: rgba(3, 129, 129, 0.8);
    box-shadow: 10px 10px 5px 2px rgba(15, 36, 36, 0.5);
    transition: 0.5s ease;
    font-size: 16px;
}


.carousel-tourism{
  margin-left: 30px;
  margin-top: 10px;
  padding-top:1px;
  min-width: 300px;
  max-width: 500px;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);

}

.exp-left{
  margin-left: 40;
  margin-right: 20;
  min-width: 350px;

}
.exp-title{
  margin-left: 40px;
  padding-top: 20px;
  padding-right:10px;
  padding-bottom: 20px;
  margin-right: 1px;   
  text-align: center;
  justify-content: center;
  align-items: center;
  position: relative;  padding: 10px;
  background-color: rgb(3, 129, 129);
  padding-bottom: 30px;
  box-shadow: 10px 10px 10px 10px rgba(0, 0, 0, 0.5);
}

.exp-head{
  color: white;
  background-color: rgb(3, 129, 129);
  padding-top: 2px;
  padding-bottom: 0px;

}
.exp-company{
  font-size: 14px;
}

.exp-role{
  color: rgb(30, 48, 133);
  font-weight: bold;
}
.exp-list{
  list-style: none;
}

.exp-container{
  background-color: white;

}

.exp-img{
  background-repeat: no-repeat;
  background-position: center;
  margin-left: 12px;
  margin-top: 14px;
  transform: scale(1);
  transition: transform 0.5s ease;

}


.exp-img:hover{
  transform: scale(1.2);
}

.overflow{
  overflow: hidden;
}