header{
    position: -webkit-sticky; 
    position: sticky; 
    z-index: 1;
}

.home-content{
    transition: 1.7s;
    justify-content: center;
    font-size: 22px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    
}

.profile-pic{
    margin-top: 30px;
    transform: scale(1);
    transition: transform 0.5s ease;
    box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.54),0 17px 50px 0 rgba(0, 0, 0, 0.54);
    background-color: rgb(238, 229, 218);
}

.profile-pic:hover{
    transform: scale(1.2);
}


.about-btn, .contact-btn{
    position: relative;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    margin-top: 40px;
    line-height: 50px;
    width: 300px;
    background-color: rgba(248, 250, 250, 0.4);
    color: #3d2b12 !important;
    letter-spacing: 0.2em;
    cursor: pointer;
    border-radius: 2px;
    transition: 0.8s ease;
}

.about-btn:hover, .contact-btn:hover {
    box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.54),0 17px 50px 0 rgba(0, 0, 0, 0.54);
    background-color: rgb(238, 229, 218);

}

.logo{
    box-shadow: 0px 10px 10px 10px rgba(0, 0, 0, 0.54),0 17px 50px 0 rgba(0, 0, 0, 0.54);
}

.navigation{
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: white;
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    z-index: 1;
    background-image: url('../Assets/nav-bg.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

