.contact-component{
    position: relative;
    width: 100%;
    text-align: center;
    justify-content: center;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: rgba(3, 129, 129, 0.7);
}

.contact-form{
    margin-top: 3px;
    margin-bottom: 20px;
    margin-left: 40px;
    padding-top:10px;
    padding-left: 30px;
    min-width: 380px;
    max-width: 380px;
    padding-bottom: 30px;
    box-shadow: 10px 10px 10px 10px rgba(0, 0, 0, 0.5);

}

.submit-btn{
    position: relative;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    margin-top: 10px;
    line-height: 50px;
    width: 200px;
    background-color: white;
    color: #00241b !important;
    cursor: pointer;
    border-radius: 2px;
    transition: 0.8s ease;
}

.contact-text{
    margin-top: 100px;
    font-size: 20px;

}

.submit-btn:hover {
    box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.54),0 17px 50px 0 rgba(0, 0, 0, 0.54);
    background-color: rgb(238, 229, 218);

}

.contactImg{
    min-width: 380px;
    max-width: 380px;
}

